:root {
  --game-message: none;
  --font-color: #615d3a;
  --game-bg: #5f6268;
  --game-cube-bg-top: #aaa3cf;
  --game-cube-bg-bottom: #d9cbcb;
  --user-inter-bg: #474748;
  --game-start: #e49bff;
  --game-start-end: #bf55ec;
  --game-stop: #c738bd;
  --game-stop-end: #a62f9e;
  --game-retry: #cb17d8;
  --game-retry-end: #850f8d;
}

/*This mixin is used as private*/

* {
  margin: 0;
  padding: 0;
}

html,
body {
  font-size: calc(1vw + 0.6em);
  box-sizing: border-box;
  font-family: "Eox2";
  color: var(--font-color, #000);
  height: 100%;
}

@font-face {
  font-family: "Eox2";
  font-weight: normal;
  font-style: normal;
  /*IE9 Compat Modes*/
  src: url("./../fonts/Exo2-Regular.eot");
  /*IE6-IE8 */
  src: url("./../fonts/Exo2-Regular.eot?#iefix") format("embedded-opentype"), url("./../fonts/Exo2-Regular.woff") format("woff"), url("./../fonts/Exo2-Regular.ttf") format("truetype");
}

.wrapper {
  background-color: #f0e9ad;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  /* Page of the game guide and result */
}

.wrapper .header {
  flex-shrink: 0;
}

.wrapper .header h1 {
  font-family: "Mistral";
  text-align: center;
}

@font-face {
  font-family: "Mistral";
  font-weight: normal;
  font-style: normal;
  /*IE9 Compat Modes*/
  src: url("./../fonts/Mistral.eot");
  /*IE6-IE8 */
  src: url("./../fonts/Mistral.eot?#iefix") format("embedded-opentype"), url("./../fonts/Mistral.woff") format("woff"), url("./../fonts/Mistral.ttf") format("truetype");
}

.wrapper .header nav ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
}

.wrapper .header nav ul li {
  margin-left: 2em;
}

.wrapper .header nav ul li a {
  font-size: 0.7em;
  text-decoration: none;
  font-weight: 600;
}

.wrapper .content {
  flex: 1 0 auto;
  max-width: 1200px;
  width: 100%;
  border: 1px solid #000;
  margin: 0 auto;
  display: flex;
  position: relative;
  overflow: hidden;
}

@keyframes gameover-text {
  0% {
    color: #fa6d42;
    font-size: 0.8rem;
  }

  25% {
    color: #f36d49;
    font-size: 1rem;
  }

  50% {
    color: #ed5d4f;
    font-size: 1.2rem;
  }

  75% {
    color: #e7557b;
    font-size: 1.4rem;
  }

  100% {
    color: #d96386;
    font-size: 1.6rem;
  }
}

@keyframes gamecomplete-text {
  0% {
    color: #7ac281;
    font-size: 0.6rem;
  }

  25% {
    color: #5be1ac;
    font-size: 0.7rem;
  }

  50% {
    color: #0aa81f;
    font-size: 0.8rem;
  }

  75% {
    color: #19992f;
    font-size: 0.9rem;
  }

  100% {
    color: #288a36;
    font-size: 1rem;
  }
}

.wrapper .content::before {
  content: "Game over";
  text-align: center;
  padding-top: 5%;
  display: var(--game-message, none);
  width: 40%;
  height: 20%;
  position: absolute;
  top: 0%;
  left: 30%;
  background: radial-gradient(#f0e9ad, var(--game-cube-bg-top));
  border: 3px solid #3e2b46;
  z-index: 2;
  font-weight: bold;
  animation: gameover-text 2s linear 2s infinite alternate;
}

.wrapper .content .content__game {
  background-color: var(--game-bg);
  position: relative;
  flex-basis: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.wrapper .content .content__game .content__level-board {
  position: absolute;
  z-index: 1;
  top: -30%;
  left: 10%;
  width: 80%;
  height: 40%;
  color: #fff;
  /*var(--font-color);*/
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 3px solid #fff;
  box-sizing: border-box;
}

.wrapper .content .content__game .content__level-board-show {
  animation: board-move-show 3s forwards ease-in-out;
}

@keyframes board-move-show {
  from {
    top: -50%;
  }

  to {
    top: 30%;
  }
}

.wrapper .content .content__game .content__level-board-show .level-board_level-number {
  flex-basis: 40%;
  padding-top: 5%;
  width: 90%;
  text-align: center;
}

.wrapper .content .content__game .content__level-board-show .level-board_level-number span:last-child {
  margin-left: 5%;
}

.wrapper .content .content__game .content__level-board-show .level-board_range-numbers {
  flex-basis: 50%;
  width: 90%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.wrapper .content .content__game .content__level-board-show .level-board_range-numbers span:last-child {
  margin-left: 5%;
}

.wrapper .content .content__game .content__level-board-hide {
  animation: board-move-hide 3s forwards ease-in-out;
}

@keyframes board-move-hide {
  from {
    top: 30%;
  }

  to {
    top: 110%;
  }
}

.wrapper .content .content__game .content__level-board-hide .level-board_level-number {
  flex-basis: 40%;
  padding-top: 5%;
  width: 90%;
  text-align: center;
}

.wrapper .content .content__game .content__level-board-hide .level-board_level-number span:last-child {
  margin-left: 5%;
}

.wrapper .content .content__game .content__level-board-hide .level-board_range-numbers {
  flex-basis: 50%;
  width: 90%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.wrapper .content .content__game .content__level-board-hide .level-board_range-numbers span:last-child {
  margin-left: 5%;
}

.wrapper .content .content__game .content__main-square {
  background-image: linear-gradient(to top, var(--game-cube-bg-bottom) 5%, var(--game-cube-bg-top) 45%);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  perspective: 500px;
  width: 80%;
  height: 80%;
  border: 1px solid #000;
  /*Increase the cube*/
  /*Decrease the cube*/
}

@keyframes cube-scale {
  0% {
    transform: scale(0, 0);
  }

  100% {
    transform: scale(1, 1);
  }
}

.wrapper .content .content__game .content__main-square .three-d-block-wrapper {
  transform: rotateY(0deg) rotateX(0deg) scale(0.1, 0.1);
  animation: cube-scale 8s 1 forwards ease-in-out paused;
}

@keyframes cube-motion {
  0% {
    transform: rotateY(0deg) rotateX(0deg);
  }

  20% {
    transform: rotateY(-180deg) rotateX(0deg);
  }

  40% {
    transform: rotateY(-360deg) rotateX(0deg);
  }

  60% {
    transform: rotateY(0deg) rotateX(0deg);
  }

  80% {
    transform: rotateY(270deg) rotateX(270deg);
  }

  100% {
    transform: rotateY(360deg) rotateX(360deg);
  }
}

.wrapper .content .content__game .content__main-square .three-d-block-wrapper .three-d-block {
  width: 450px;
  height: 450px;
  transform-style: preserve-3d;
  animation-name: cube-motion;
  animation-duration: 8s;
  animation-direction: normal;
  animation-timing-function: ease-in-out;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: paused;
}

.wrapper .content .content__game .content__main-square .three-d-block-wrapper .three-d-block .three-d-block__side {
  position: absolute;
  width: inherit;
  height: inherit;
}

.wrapper .content .content__game .content__main-square .three-d-block-wrapper .three-d-block .three-d-block_center {
  background-image: radial-gradient(circle, rgb(230, 221, 224) 0%, rgb(90, 119, 147) 100%);
  transform: translateZ(225px);
